@import "../variable";
@import url("https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700");
@import url("https://fonts.googleapis.com/css?family=Montserrat:300,400,500,600,700");

body {
	background-color: #ffffff;
	color: #aeaeae;
	font-size: 15px;
	line-height: 1.6;
	font-family: "Poppins", serif;
}

a {
	color: #aeaeae;
	text-decoration: none;
}
a.normalLink {
	color: #337ab7;
}
blockquote,
a h1,
a h2,
a h3,
a h4,
a h5,
a h6,
h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a,
h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4 {
	color: #000000;
	font-family: "Montserrat", sans-serif;
	font-weight: 400;
	line-height: 1.1;
}

.teammembers img,
.blog-box,
.cop-logo img,
.service-box,
a {
	outline: none;
	text-decoration: none !important;
	-webkit-transition: all 0.3s ease-in-out;
	-moz-transition: all 0.3s ease-in-out;
	-ms-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
}

a:hover,
a:focus {
	color: #000000;
}

.check li:before {
	content: "\f00c";
	font-family: "FontAwesome";
	font-size: 16px;
	left: 0;
	padding-right: 10px;
	position: relative;
	top: 2px;
}

.check li {
	font-size: 13px;
	list-style: none;
	margin-bottom: 8px;
}

.check li:last-child {
	margin-bottom: 0;
}

.check {
	margin-left: 0;
	padding-left: 0;
}

/*********************
MENU
*********************/

.vertical-header {
	position: fixed;
	// left: 0;
	z-index: 1111;
	top: 51px;
	bottom: 0;
	-webkit-transform: translate3d(0, 0, 0);
	-moz-transform: translate3d(0, 0, 0);
	-o-transform: translate3d(0, 0, 0);
	-ms-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
}

.vertical-header-wrapper {
	width: 100%;
	height: 100%;
	z-index: 10;
	min-width: 260px;
	opacity: 0.8;
	background: #000;
}

.subsub-menu-wrapper,
.sub-menu-wrapper {
	opacity: 0;
	visibility: hidden;
	background-color: #151515;
	position: absolute;
	padding: 0px 10px 0;
	width: 100%;
	top: 0;
	max-width: 240px;
	bottom: 0;
	left: 60%;
	height: 100%;
	display: table;
	-webkit-transition: visibility 0.35s ease-in-out, opacity 0.35s ease-in-out, top 0.35s ease-in-out,
		bottom 0.35s ease-in-out, left 0.35s ease-in-out, right 0.35s ease-in-out;
	-moz-transition: visibility 0.35s ease-in-out, opacity 0.35s ease-in-out, top 0.35s ease-in-out,
		bottom 0.35s ease-in-out, left 0.35s ease-in-out, right 0.35s ease-in-out;
	-o-transition: visibility 0.35s ease-in-out, opacity 0.35s ease-in-out, top 0.35s ease-in-out,
		bottom 0.35s ease-in-out, left 0.35s ease-in-out, right 0.35s ease-in-out;
	transition: visibility 0.35s ease-in-out, opacity 0.35s ease-in-out, top 0.35s ease-in-out, bottom 0.35s ease-in-out,
		left 0.35s ease-in-out, right 0.35s ease-in-out;
}

.subsub-menu-wrapper {
	background-color: #191919;
}
.primary-menu {
	height: auto;
	overflow: auto;
}
.primary-menu li:hover .subsub-menu li:hover a,
.primary-menu li:hover .sub-menu-wrapper li:hover a,
.primary-menu li:hover a {
	color: rgba(255, 255, 255, 1);
}

.primary-menu li:hover .subsub-menu li a,
.primary-menu li:hover .sub-menu-wrapper li a,
.menu-search .btn,
.vertical-header li a {
	color: rgba(255, 255, 255, 0.7);
}

.primary-menu li:hover .subsub-menu li a {
	color: rgba(255, 255, 255, 0.7) !important;
}

.primary-menu li:hover .subsub-menu li:hover a {
	color: rgba(255, 255, 255, 1) !important;
}

.vertical-header li {
	list-style: none;
}

.vertical-header ul {
	padding: 0;
	max-height: 85vh;
}
.overlay {
	background: url(../images/pattern.png) repeat rgba(0, 0, 0, 0.7);
	overflow: hidden;
}
.application-wrapper {
	display: grid;
	grid-template-columns: 0% 100% 0%;
	margin: 4px auto 0 auto;
	@media screen and (min-width: 1920px) {
		//For Projectors or Higher Resolution Screens (Full HD)
		grid-template-columns: 15% 70% 15%;
		margin: 4px auto 0 auto;
	}
	@media screen and (min-width: 3840px) {
		//For 4K Displays (Ultra HD)
		grid-template-columns: 25% 50% 25%;
		margin: 4px auto 0 auto;
	}
	// mobile
	@media (max-width: 768px) {
		margin-top: 51px;
	}
	//landscape
	@media (min-width: 992px) and (max-width: 1199px) {
		margin-top: 4px;
	}
	// portrait
	@media (min-width: 768px) and (max-width: 991px) {
		margin-top: 51px;
	}
	//print
	@media print {
		margin-top: 0;
	}
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
	.vertical-header ul {
		padding: 3rem 0;
	}
}
.menuHeading {
	color: $fluorescent-color;
}

.sub-menu li:hover .subsub-menu-wrapper,
:hover .sub-menu-wrapper {
	opacity: 1;
	z-index: 1000;
	left: 100%;
	position: absolute;
	visibility: visible;
}

.primary-menu li a {
	display: block;
	width: 100%;
	position: relative;
	text-decoration: none !important;
}

.primary-menu li a:hover {
	color: $fluorescent-color;
	font-weight: bold;
}

.primary-menu li .fa-angle-right {
	position: absolute;
	right: 0;
	font-size: 14px;
	top: 0px;
}

.yamm .fa-shopping-bag,
.navbar-inverse .fa-shopping-bag,
.primary-menu .fa-shopping-bag {
	margin-left: 4px;
}

.primary-menu {
	list-style: none;
}

@media (max-width: 768px) {
	.primary-menu {
		list-style: none;
	}
}

.primary-menu li {
	text-transform: capitalize;
	padding: .75rem 1rem;
	display: flex;
	font-size: 12px;
	line-height: 1 !important;
	margin: 0 !important;
	text-transform: capitalize;
	white-space: nowrap;
	font-family: "Montserrat", sans-serif;
	a {
		&.admin {
			color: $fluorescent-color;
		}
	}
}

.primary-menu li:first-child {
	border-top: 0;
}

.primary-menu li:last-child {
	border-bottom: 0;
}

.center-content {
	display: table-cell;
	vertical-align: middle;
}

.margin-block {
	min-height: 40px;
}

.audiBtn {
	border-radius: 100%;
	color: #000;
	background: #bdd037;
	border: solid 1px #bdd037;
	outline: 0;
	padding: 0;
	font-size: 12px;
	width: 25px;
	height: 25px;
}
.audiBtn.mobile {
	position: fixed;
	bottom: 7px;
	right: 10px;
	padding: 0;
	width: 40px;
	height: 40px;
	font-size: 15px;
	border: solid 1px #bdd037;
	z-index: 100;
	box-shadow: 1px 2px 20px #000;
}

.menu-search {
	padding: 0 15px;
}

.menu-search .form-group {
	position: relative;
	margin: 0;
	padding: 0;
}

.menu-search .btn {
	background-color: transparent !important;
	position: absolute;
	border: 0;
	top: -1px;
	padding-right: 10px;
	padding-left: 10px;
	font-size: 12px;
	right: 0;
}

.menu-search .form-control:focus .btn {
	color: #f4af0b !important;
}

.menu-search .form-control {
	border-radius: 0;
	background-color: transparent;
	height: 40px;
	border: 0;
	color: rgba(255, 255, 255, 0.7);
	font-size: 11px;
	box-shadow: none !important;
	/* border-top: 1px dotted rgba(255, 255, 255, 0.15);
    border-bottom: 1px dotted rgba(255, 255, 255, 0.15); */
}

.menu-search .form-control::-moz-placeholder {
	color: rgba(255, 255, 255, 0.7);
	opacity: 1;
}

.menu-search .form-control:-ms-input-placeholder {
	color: rgba(255, 255, 255, 0.7);
}

.menu-search .form-control::-webkit-input-placeholder {
	color: rgba(255, 255, 255, 0.7);
}

.menu-social ul {
	margin: 0;
	padding: 0;
	background: transparent;
}

.menu-social ul li {
	margin-top: 0;
	padding-top: 0;
}

.menu-social ul li i {
	font-size: 12px;
	padding: 12px;
	border: 1px solid rgba(255, 255, 255, 0.2);
	border-radius: 100%;
	width: 40px;
	height: 40px;
}
.menu-social ul li:hover i {
	color: $fluorescent-color;
}

ul.mobile-menu-social {
	list-style: none;
	position: absolute;
	left: -40px;
	background: #000;
}
ul.mobile-menu-social li.google-mobile button {
	border-radius: 50% !important;
	width: 40px;
	height: 40px;
	background: transparent;
}
ul.mobile-menu-social li.google-mobile button div {
	padding-left: 10px !important;
	background: transparent !important;
	padding-top: 15px !important;
	border-radius: 50% !important;
}
.mobile-menu-social li:not(:last-child) {
	border-radius: 30px;
	width: 40px;
	border: solid 1px $fluorescent-color;
	height: 40px;
	padding: 7px;
	margin: 0px 0px 10px 5px;
}
.mobile-menu-social li a {
	color: $fluorescent-color;
}
