SECTIONS
*********************/

.video-section {
	position: relative;
	width: 100%;
	height: calc(100vh - 52px);
	overflow: hidden;
	margin-top: 48px;
	@media only screen and (max-width: 767px) {
		margin: 0;
	}
	@media (min-width: 768px) and (max-width: 991px) {
		margin-top: -3px;
	}
	.banner-img {
		opacity: .8;
		object-fit: cover;
	}
}
.container {
	width: 100%;
	max-width: 1180px;
}

.section {
	padding: 10px 0px;
	position: relative;
	overflow: hidden;
	display: block;
	background-color: #000 !important;
	opacity: 0.8;
	margin-top: 47px;
}

@media (max-width: 767px) {
	.section {
		margin-top: 1px;
	}
}
/* Landscape tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {
	.section {
	}
}
/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {
	.section {
		margin-top: -3px;
	}
}

.section.noover {
	overflow: visible !important;
}

.webContent {
	background: $fluorescent-color;
	@media (max-width: 767px) {
		height: 50px;
		line-height: 2;
		width: 80%;
	}
	a {
		color: $black-color !important;
		text-align: center;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		display: block;
		font-size: 14px;
	}
}
.nav-header {
	background: $black-two;
	display: grid;
	grid-template-columns: 67% 33%;
	align-items: center;
	padding: 0 !important;
	max-width: 260px;
	li {
		padding: 1rem;
	}
}

.text-widget h3,
.section-title h3 {
	margin: 0 0 25px;
	padding: 0;
	font-size: 28px;
	font-weight: 600;
}

.section-title p {
	max-width: 800px;
	margin: 0 auto;
	padding: 0;
}

.section.lb {
	background-color: #f7f6f6;
}

.section.db {
	background-color: #000000;
}

.nopadtop {
	padding-top: 0;
}

.blog-box:hover,
.service-box:hover,
.shadow {
	box-shadow: 0px 7px 14px rgba(0, 0, 0, 0.1);
}

.parallax {
	background-attachment: fixed;
	background-position: 50% 0;
	background-repeat: no-repeat;
	height: 100%;
	position: relative;
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	background-size: cover;
}

.parallax.parallax-off {
	background-attachment: scroll;
	display: block;
	height: 100%;
	min-height: 100%;
	overflow: hidden;
	position: relative;
	background-position: top center;
	vertical-align: sub;
	width: 100%;
	z-index: 2;
}

.no-scroll-xy {
	overflow: hidden !important;
	-webkit-transition: all 0.4s ease-in-out;
	-moz-transition: all 0.4s ease-in-out;
	-ms-transition: all 0.4s ease-in-out;
	-o-transition: all 0.4s ease-in-out;
	transition: all 0.4s ease-in-out;
}

