body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }
  body * {
    outline: 0; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.btn-bni {
  background: #c2d82e;
  color: #000 !important;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }
  .btn-bni:focus, .btn-bni:hover {
    border: 0;
    outline: 0 !important; }
  .btn-bni:active {
    opacity: 0.5 !important; }
  .btn-bni.sm {
    padding: 8px;
    font-weight: 450;
    border-radius: 5px; }
  .btn-bni i {
    line-height: 1.3; }

.btn-bni-sm {
  background: #333;
  color: #c2d82e;
  border-radius: 20px;
  font-size: 1.2rem;
  border: solid 1px #000;
  outline: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.btn-capsule {
  background: #333;
  color: #fff !important;
  cursor: pointer;
  font-weight: 500;
  height: 25px;
  line-height: 22px;
  width: 100px; }
  .btn-capsule.btn-sm {
    padding: 0 1rem;
    font-size: 12px;
    border-radius: 15px; }
  .btn-capsule:hover, .btn-capsule.active {
    color: #c2d82e !important;
    outline: 0 !important;
    border: solid 1px #c2d82e; }

.downloadIcon:before {
  color: #b9c134;
  font-size: 30px !important; }

.relativeSpinner {
  text-align: center;
  padding: 1rem;
  width: 100%; }

.spinner {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%); }

